@import "variables";
.banner {
  height: 840px;
  background-size: cover;
  background-position: center;
  background-image: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/banner.png?raw=true);
  .banner-title {
    position: absolute;
    top: 458px;
    right: 400px;
    .h2-subtitle {
      text-align: right;
      color: #650300;
      font-size: 36px;
      line-height: 1.5;
      font-weight: bold;
    }
    .h2-title {
      color: #650300;
      font-size: 64px;
      line-height: 1.5;
      font-weight: bold;
    }
  }
}
.profession {
  background-color: #AA06010D;
  padding: 64px 40px;
  .h3-title {
    font-size: 48px;
    line-height: 1.5;
    font-weight: bold;
    color: #650300;
  }
  ul {
    display: flex;
    justify-content: space-around;
  }
  li {
    display: flex;
    flex-direction: column;
    width: 208px;
    margin-bottom: 40px;
    img {
      display: block;
      height: 100px; // 圖片寬高不同
      margin-bottom: 40px;
    }
  }
  .h4-title {
    color: #650300;
    font-size: 24px;
    line-height: 1.5;
    font-weight: bold;
    text-align: center;
  }
  .pro-description {
    font-family: 'Noto Sans';
    font-size: 16px;
    line-height: 1.5;
  }
}
.classic {
  padding: 64px 0px 48px 0px;
  max-width: 1200px;
  .h3-title {
    font-size: 48px;
    line-height: 1.5;
    color: #650300;
    font-weight: bold;
    text-align: center;
  }
  ul {
    li {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 32px;
      h4 {
        width: 635px;
        display: flex;
        align-items: center;
        color: $primary-color;
        font-size: 48px;
        padding-left: 30px;
        font-weight: bold;
        line-height: 1.5;
        font-style: italic;
      }
      img {
        max-width: 635px;
      }
    }
    .second-item {
      display: flex;
      flex-direction: row-reverse;
    }
  }
}
.design {
  height: auto;
  background-size: cover;
  background-position: center;
  background-image: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/bg.png?raw=true);
  padding: 64px 0px 80px 0px;
  h3 {
    font-family: 'Noto Sans';
    font-weight: bold;
    color: #FFFFFF;
    font-size: 48px;
    line-height: 1.5;
    text-align: center;
    margin-bottom: 60px;
  }
  ul {
    display: flex;
    justify-content: space-around;
    max-width: 1110px;
    li {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
  h4 {
    font-size: 72px;
    line-height: 1.5;
    color: #AA0601;
    font-weight: bold;
  }
  .design-item1 {
    width: 540px;
    height: 472px;
    background-size: cover;
    background-position: center;
    background-image: url(https://hexschool.github.io/webLayoutTraining1st/week3/product-8.png);
    opacity: 0.5;
  }
  .design-item2 {
    width: 540px;
    height: 472px;
    background-size: cover;
    background-position: center;
    background-image: url(https://hexschool.github.io/webLayoutTraining1st/week3/product-9.png);
    opacity: 0.5;
  }
}

.suggesters {
  padding: 64px 0px;
  h4 {
    font-size: 48px;
    line-height: 1.5;
    font-weight: bold;
    color: #650300;
    text-align: center;
    margin-bottom: 60px;
  }
  ul {
    max-width: 1110px;
    display: flex;
    justify-content: space-between;
    li {
      width: 255px;
      border: 1px solid #DEE2E6;
      margin-bottom: 28px;
      img {
        width: 100%;
      }
      .suggest {
        padding: 16px;
        h5 {
          font-size: 14px;
          line-height: 1.5;
        }
        p {
          font-size: 16px;
          line-height: 1.5;
        }
        .edited-time {
          color: #707070;
        }
      }
    }
  }
  
}
.contact-us {
background-color: #AA06010D;
padding: 64px 0 80px 0;
}
.contact {
  max-width: 730px;
  h4 {
    font-size: 48px;
    line-height: 1.5;
    font-weight: bold;
    text-align: center;
    margin-bottom: 20px;
  }
  p {
    font-size: 20px;
    line-height: 1.5;
    margin-bottom: 20px;
  }
  form {
    h5 {
      font-size: 16px;
      line-height: 1.5;
      margin-bottom: 4px;
    }
  }
  .form-name, .form-phone, .form-mail,.form-response, .form-response {
    width: 100%;
    margin-bottom: 20px;
  }
  .agreement {
    display: flex;
    input {
      margin-top: 6px;
    }
    font-size: 16px;
    line-height: 1.5;
    margin-bottom: 42px;
    p {
      margin-bottom: 0;
    }
  }
  .submit-btn {
    display: block;
    margin: 0 auto;
    width: 350px;
    padding: 16px 143px;
    color: #FFFFFF;
    background-color: #AA0601;
  }
}

@media (max-width: 992px) {
  .banner {
    background-image: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/banner-sm.png?raw=true);
    .banner-title {
      top: 480px;
      right: 40px;
      .h2-title {
        font-size: 48px;
      }
      .h2-subtitle {
        font-size: 20px;
      }
    }
  }
  .profession {
    .h3-title {
      font-size: 32px;
    }
    ul {
      flex-wrap: wrap;
      li {
      width: 49%;
      p {
        width: 237px;
        margin: 0 auto;
      }
      }
    }
  }
  .classic {
    max-width: 690px;
    .h3-title {
      font-size: 32px;
    }
    ul {
      li {
        .classic-avatar {
          width: 390px;
        }
        h4 {
          font-size: 28px;
          line-height: 1.5;
          img {
            width: 32px;
            height: 32px;
          }
        }
      }
      .second-item {
        display: flex;
        flex-direction: row;
      }
    }
  }
  .design {
    h3 {
      font-size: 32px;
    }
    ul {
      max-width: 768px;
      li {
        h4 {
          font-size: 32px;
        }
      }
      .design-item1, .design-item2 {
        width: 330px;
        height: 280px;
      }
    }
  }
  .suggesters {
    h4 {
      font-size: 32px;
    }
    ul {
      max-width: 768px;
      flex-wrap: wrap;
      li {
        width: 45%;
      }
    }
  }
  .contact {
    max-width: 450px;
    h4 {
      font-size: 32px;
    }
    p {
      font-size: 16px;
    }
    form {
      h5 {
        font-size: 14px;
        margin-bottom: 2px;
      }
      .agreement p {
        font-size: 16px;
        line-height: 1.5;
      }
    }
    .submit-btn {
      width: 210px;
      padding: 8px 76px;
    }
  }
}


@media (max-width: 768px) {
  .banner {
    height: 394px;
    .banner-title {
      top: 352px;
      right: 15px;
      .h2-title {
        font-size: 32px;
      }
      .h2-subtitle {
        font-size: 16px;
      }
    }
  }
  .profession {
    .h3-title {
      font-size: 24px;
      line-height: 2;
    }
    ul {
      flex-wrap: wrap;
      li {
      width: 90%;
      }
    }
  }
  .classic {
    width: 345px;
    .h3-title {
      font-size: 24px;
      line-height: 2;
    }
    ul {
      li {
        .classic-avatar {
          display: none;
        }
        h4 {
          justify-content: center;
          color: #FFFFFF;
          padding: 0;
          img {
            color: #FFFFFF;
          }
        }
      }
      .first-item {
        height: 134px;
        background-size: cover;
        background-position: center;
        background-image: url(https://hexschool.github.io/webLayoutTraining1st/week3/product-1.png);
      }
      .second-item {
        height: 134px;
        background-size: cover;
        background-position: center;
        background-image: url(https://hexschool.github.io/webLayoutTraining1st/week3/product-2.png);
      }
      .third-item {
        height: 134px;
        background-size: cover;
        background-position: center;
        background-image: url(https://hexschool.github.io/webLayoutTraining1st/week3/product-3.png);
      }
    }
  }
  .design {
    outline: 2px solid tomato;
    padding: 32px 0 16px 0;
    h3 {
      font-size: 20px;
      line-height: 48px;
      margin-bottom: 20px;
    }
    ul {
      width: 345px;
      flex-wrap: wrap;
      li {
        h4 {
          font-size: 32px;
        }
      }
      .design-item1, .design-item2 {
        height: 284px;
        margin-bottom: 16px;
      }
    }
  }
  .suggesters {
    h4 {
      font-size: 20px;
      line-height: 48px;
      margin-bottom: 20px;
    }
    ul {
      max-width: 345px;
      padding-right: 15px;
      padding-left: 15px;
      li {
        width: 100%;
      }
    }
  }
  .contact {
    max-width: 345px;
    h4 {
      font-size: 20px;
    }
    .submit-btn {
      width: 165px;
      padding: 8px 54px;
    }
  }
}